import { supabase } from './supabase';

export interface ChatResponse {
  verse: string;
  reference: string;
  relevance: string;
  explanation: string;
}

export interface ChatItem {
  id: string;
  question: string;
  response: ChatResponse;
  created_at: string;
  is_archived: boolean;
}

export const api = {
  // Chat operations
  async loadChats(): Promise<{ chats: ChatItem[], archivedChats: ChatItem[] }> {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) {
      throw new Error('No user found');
    }

    const { data: chatData, error } = await supabase
      .from('chats')
      .select(`
        id,
        user_id,
        question,
        response,
        created_at,
        is_archived
      `)
      .eq('user_id', user.id)
      .order('created_at', { ascending: false });

    if (error) {
      throw new Error(`Error loading chats: ${error.message}`);
    }

    const processedChats = (chatData || []).map(chat => ({
      ...chat,
      response: typeof chat.response === 'string' 
        ? JSON.parse(chat.response)
        : chat.response
    }));

    return {
      chats: processedChats.filter(chat => !chat.is_archived),
      archivedChats: processedChats.filter(chat => chat.is_archived)
    };
  },

  async archiveChat(chatId: string, currentlyArchived: boolean): Promise<void> {
    const { error } = await supabase
      .from('chats')
      .update({ is_archived: !currentlyArchived })
      .eq('id', chatId);

    if (error) {
      throw new Error(`Error toggling archive status: ${error.message}`);
    }
  },

  async deleteChat(chatId: string): Promise<void> {
    const { error } = await supabase
      .from('chats')
      .delete()
      .eq('id', chatId);

    if (error) {
      throw new Error(`Error deleting chat: ${error.message}`);
    }
  },

  async createChat(question: string, response: ChatResponse): Promise<ChatItem> {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) {
      throw new Error('No user found');
    }

    const { data, error } = await supabase
      .from('chats')
      .insert([
        {
          user_id: user.id,
          question,
          response,
          is_archived: false
        }
      ])
      .select()
      .single();

    if (error) {
      throw new Error(`Error creating chat: ${error.message}`);
    }

    return data;
  },

  // Auth operations
  async signOut(): Promise<void> {
    const { error } = await supabase.auth.signOut();
    if (error) {
      throw new Error(`Error signing out: ${error.message}`);
    }
  }
};
